<template>
  <div class="banner"></div>
  <div class="container">
    <!-- 購物車列表 -->
    <div v-if="cart.carts.length === 0" class="text-center py-5">
      <p class="h2 mb-5">購物車尚無品項，趕緊去選購吧！</p>
      <button
        type="button"
        class="btn btn-outline-secondary"
        @click="$router.push('/products/all')"
      >
        <i class="bi bi-arrow-left me-2"></i>繼續選購
      </button>
    </div>
    <div v-if="cart.carts.length > 0" class="row justify-content-center mt-5">
      <div class="col-md-6">
        <h1 class="text-center">購物車</h1>
        <div class="text-end">
          <button
            class="btn btn-outline-danger"
            type="button"
            :disabled="!hasCartsItems"
            @click="deleteAllCarts"
          >
            清空購物車
          </button>
        </div>
        <table class="table align-middle mb-3">
          <thead>
            <tr>
              <th></th>
              <th>品名</th>
              <th style="width: 150px">數量/單位</th>
              <th>單價</th>
            </tr>
          </thead>
          <tbody>
            <template v-if="cart.carts">
              <!-- 判斷 購物車是否有資料 -->
              <tr v-for="item in cart.carts" :key="item.id">
                <td>
                  <button
                    type="button"
                    title="刪除"
                    class="btn btn-outline-danger btn-sm"
                    @click="removeCartItem(item.id)"
                    :disabled="loadingStatus.loadingItem === item.id"
                  >
                    <i class="bi bi-trash"></i>
                  </button>
                </td>
                <td>
                  {{ item.product.title }}
                  <div class="text-success" v-if="item.coupon">
                    已套用優惠券
                  </div>
                </td>
                <td>
                  <div class="input-group input-group-sm">
                    <div class="input-group">
                      <input
                        class="form-control text-center"
                        type="number"
                        min="1"
                        v-model.number="item.qty"
                        @blur="updateCart(item)"
                        :disabled="loadingStatus.loadingItem === item.id"
                      />
                      <span class="input-group-text" id="basic-addon2">
                        {{ item.product.unit }}
                      </span>
                    </div>
                  </div>
                </td>
                <td class="text-end">
                  <small v-if="cart.final_total !== cart.total" class="text-success"
                    >折扣價：</small
                  >
                  {{ $filters.currency(item.final_total) }}
                </td>
              </tr>
            </template>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-end">總計</td>
              <td class="text-end">{{ $filters.currency(cart.total) }}</td>
            </tr>
            <tr v-if="cart.final_total !== cart.total">
              <td colspan="3" class="text-end text-success">折扣價</td>
              <td class="text-end text-success">{{ $filters.currency(cart.final_total) }}</td>
            </tr>
          </tfoot>
        </table>
        <div class="row mb-5">
          <div class="col-6">
            <button
              type="button"
              class="btn btn-outline-secondary btn-block"
              @click="$router.push('/products/all')"
            >
              <i class="bi bi-arrow-left me-2"></i>繼續選購
            </button>
          </div>
          <div class="col-6">
            <button
              type="button"
              class="btn btn-primary btn-block"
              @click="$router.push('/cartsCheckout')"
            >
              <span class="me-2">前往結帳</span>
              <i class="bi bi-arrow-right"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="bg-light py-3">
    <div class="container">
      <div class="row justify-content-between">
        <div class="col-12">
          <h2 class="mb-3 ">推薦商品</h2>
        </div>
        <div class="col-md-6 col-lg-4 col-xl-3 mb-4" v-for="item in randomProducts" :key="item.id">
          <div class="card" @click="pushProductPage(item)">
            <div
              class="card__img position-relative"
              :style="{ backgroundImage: `url('${item.imageUrl}')` }"
            >
              <span
                v-if="item.options.sell_status"
                class="bg-danger text-white d-inline-block px-2 py-1"
                >{{ item.options.sell_status }}</span
              >
            </div>
            <div class="card-body d-flex flex-column justify-content-center">
              <h2 class="card-title h5">{{ item.title }}</h2>
              <div class="h5 text-center py-1" v-if="item.price">
                <span
                  v-if="item.price != item.origin_price"
                  class="h6 text-decoration-line-through"
                >
                  ${{ $filters.currency(item.origin_price) }}
                </span>
                <span v-if="item.price != item.origin_price" class="h4 text-danger fw-bold">
                  ${{ $filters.currency(item.price) }}
                </span>
                <span v-if="item.price == item.origin_price" class="h4 fw-bold">
                  ${{ $filters.currency(item.price) }}
                </span>
                元
              </div>
              <div class="btn-group btn-group-sm">
                <button
                  type="button"
                  class="btn btn-secondary"
                  @click.stop="addCart(item.id, 1)"
                  :disabled="loadingStatus.loadingItem === item.id || !item.is_enabled"
                >
                  加到購物車
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!-- vue-loading -->
  <Loading :active="isLoading"></Loading>
</template>
<script>
import emitter from '../methods/eventBus';

// 取亂數
function getRandomInt(max) {
  return Math.floor(Math.random() * max);
}

export default {
  data() {
    return {
      cart: {
        total: 0,
        final_total: 0,
        carts: [],
      },
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
      hasCartsItems: false,
      loadingStatus: {
        loadingItem: '',
      },
      isLoading: false,
      coupon_code: '',
      products: [],
      randomProducts: [],
    };
  },
  methods: {
    getCart() {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http
        .get(url)
        .then((res) => {
          this.isLoading = false;
          if (res.data.success) {
            this.cart = res.data.data;
          } else {
            this.$httpMessageState(res, res.data.message);
          }
        })
        .catch((error) => {
          console.dir(error);
        });
    },
    updateCart(item) {
      this.isLoading = true;
      this.loadingStatus.loadingItem = item.id;
      const data = {
        product_id: item.product_id,
        qty: item.qty,
      };
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart/${item.id}`;
      this.$http
        .put(url, { data })
        .then((res) => {
          this.isLoading = false;
          this.loadingStatus.loadingItem = '';
          this.$httpMessageState(res, res.data.message);
          if (res.data.success) {
            this.getCart();
          }
        })
        .catch((error) => {
          console.dir(error);
        });
    },
    removeCartItem(id) {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart/${id}`;
      this.$http
        .delete(url)
        .then((res) => {
          this.isLoading = false;
          this.$httpMessageState(res, res.data.message);
          if (res.data.success) {
            this.getCart();
            emitter.emit('update-cartNum'); // 更新購物車icon顯示數量
          }
        })
        .catch((error) => {
          console.dir(error);
        });
    },
    deleteAllCarts() {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/carts`;
      this.$http
        .delete(url)
        .then((res) => {
          this.isLoading = false;
          this.$httpMessageState(res, res.data.message);
          if (res.data.success) {
            this.getCart();
            emitter.emit('update-cartNum'); // 更新購物車icon顯示數量
          }
        })
        .catch((error) => {
          console.dir(error);
        });
    },
    createOrder() {
      this.isLoading = true;
      const order = this.form;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/order`;
      this.$http
        .post(url, { data: order })
        .then((res) => {
          this.isLoading = false;
          this.$httpMessageState(res, '建立訂單');
          if (res.data.success) {
            emitter.emit('update-cartNum'); // 更新購物車icon顯示數量
            this.$router.push(`/checkout/${res.data.orderId}`);
            // 清空表單資料
            this.$refs.form.resetForm();
            this.form.message = '';
          }
        })
        .catch((error) => {
          console.dir(error);
        });
    },
    addCouponCode() {
      const data = {
        code: this.coupon_code,
      };
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/coupon`;
      this.$http.post(url, { data }).then((res) => {
        this.isLoading = false;
        if (res.data.success) {
          this.$httpMessageState(res, '套用優惠券');
          this.getCart();
        } else {
          this.$httpMessageState(res, res.data.message);
        }
      });
    },
    getProductsAll() {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products/all`;
      this.$http
        .get(url)
        .then((res) => {
          this.isLoading = false;
          if (res.data.success) {
            this.products = res.data.products;
            this.getRandomProducts();
          } else {
            this.$httpMessageState(res, res.data.message);
          }
        })
        .catch((error) => {
          console.dir(error);
        });
    },
    getRandomProducts() {
      // 隨機推薦產品
      const randomArr = new Set([]);
      const maxSize = this.products.length < 4 ? this.products.length : 4;

      // 取得不重複亂數
      for (let index = 0; randomArr.size < maxSize; index + 1) {
        const randomNum = getRandomInt(this.products.length);
        randomArr.add(randomNum);
      }

      this.randomProducts = [];
      randomArr.forEach((item) => {
        this.randomProducts.push(this.products[item]);
      });
    },
    addCart(id, qty = 1) {
      this.isLoading = true;
      const data = {
        product_id: id,
        qty,
      };
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http
        .post(url, { data })
        .then((res) => {
          this.isLoading = false;
          this.$httpMessageState(res, res.data.message);
          if (res.data.success) {
            this.qty = 1;
            emitter.emit('update-cartNum'); // 更新購物車icon顯示數量
            this.getCart();
            // this.$router.push('/products');
          }
        })
        .catch((error) => {
          console.dir(error);
        });
    },
  },
  watch: {
    cart() {
      // 購物車是否有資料， length>0:true, length=0:false
      this.hasCartsItems = !!this.cart.carts.length;
    },
  },
  created() {
    this.getCart();
    this.getProductsAll();
  },
};
</script>

<style lang="scss" scoped>
.banner {
  height: calc(30vh);
  background-image: url(https://images.unsplash.com/photo-1476041800959-2f6bb412c8ce?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1950&q=80);
  background-position: 50% 55%;
  background-size: cover;
  background-repeat: no-repeat;
}

.btn-block {
  display: block;
  width: 100%;
}

.card {
  &:hover {
    box-shadow: 0 0.125rem 0.25rem rgba(#000, 0.075);
  }
  .card-title {
    max-width: 100%;
  }
  .card__img {
    width: 100%;
    height: 250px;
    background-size: cover;
    background-position: center;
  }
}
</style>
